import { requestStatsInfoApi, ResStatsInfo } from '@/services/apis/index.api';
import { action, observable } from 'mobx';

class IndexModel {
  @observable statsInfo: ResStatsInfo = Object.create(null);

  /**
   * 获取统计信息
   */
  @action
  async getStatsInfo(id = '') {
    // 获取统计信息
    const { data } = await requestStatsInfoApi({ id });
    this.statsInfo = data;
  }
}

export default new IndexModel();

