import { Http } from '@hb/taro-fusion-components';
import { getStatsInfoAction } from '../actions';

export interface ReqStatsInfo {
  id: string;
}

export interface ResStatsInfo {
  catHouseNum: number;
  userNum: number;
  tnrCatNum: number;
  hospitalNum: number;
}

/**
 * 获取统计信息
 * @param data
 * @returns
 */
export function requestStatsInfoApi(data: ReqStatsInfo) {
  return Http.post<ReqStatsInfo, ResStatsInfo>(getStatsInfoAction, data);
}
